#page {
  display: flex;
  background-color: #d1d5db;
}

input {
  margin: 5px 5px 5px 5px;
}

form {
  flex: 1;
  margin-bottom: 4%;
  text-align: center;
}

table {
  margin-top: 20px;
  margin-left: auto;
  margin-right: auto;
}

td,
tr {
  border: 1px solid black;
}

h1 {
  margin-top: 0;
  margin-bottom: 2.5%;
  font-family: "Inter", -apple-system, BlinkMacSystemFont, "Segoe UI", Helvetica,
    Arial, sans-serif, "Apple Color Emoji", "Segoe UI Emoji";
  color: #121828;
}

p {
  color: red;
  font-weight: bold;
}

p:hover {
  cursor: pointer;
}

img {
  width: 75px;
  height: 75px;
}

button {
  margin-bottom: 2.5%;
}

b {
  color: green;
}

b:hover {
  cursor: pointer;
}

label {
  display: inline-block;
  clear: left;
  width: 190px;
  text-align: left;
}

input {
  display: inline-block;
}

.campus {
  width: 135px;
}

.addCampus {
  width: 105px;
}
